<template>
    <div>
        <i>{{student.id}}</i>
        {{student.firstName}}
        {{student.secondName}}
        {{student.course}}
        {{student.faculty}}
        <span>
            <input type="button" value="Edit" @click="edit">
            <input type="button" value="Remove" @click="del">
        </span>
    </div>
</template>

<script>
export default {
  name: 'StudentRow',
  props: ['student'],
  methods: {
    edit () {
      this.$emit('editStudent', this.student)
    },
    del () {
      this.$emit('deleteStudent', this.student)
    }
  }
}
</script>

