<template>
    <div>
        <input type="text" placeholder="First name" v-model="firstName">
        <input type="text" placeholder="Second name" v-model="secondName">
        <input type="text" placeholder="Middle name" v-model="middleName">
        <input type="text" placeholder="Course" v-model="course">
        <input type="text" placeholder="Faculty" v-model="faculty">
        <input type="text" placeholder="Team" v-model="team">
        <input type="button" value="Save" @click="save">
    </div>
</template>

<script>
function getIndex (list, id) {
  for (let i = 0; i < list.length; i++) {
    if (list[i].id === id) {
      return i
    }
  }
  return -1
}
export default {
  name: 'StudentForm',
  props: ['students', 'currentStudent'],
  data () {
    return {
      id: '',
      firstName: '',
      secondName: '',
      middleName: '',
      course: '',
      faculty: '',
      team: ''
    }
  },
  watch: {
    currentStudent (currStudent) {
      this.id = currStudent.id
      this.firstName = currStudent.firstName
      this.secondName = currStudent.secondName
      this.middleName = currStudent.middleName
      this.course = currStudent.course
      this.faculty = currStudent.faculty
      this.team = currStudent.team
    }
  },
  methods: {
    save () {
      let student =
        {
          firstName: this.firstName,
          secondName: this.secondName,
          middleName: this.middleName,
          course: this.course,
          faculty: this.faculty,
          team: this.team
        }

      if (this.id) {
        this.$resource('api/user/student/{id}')
          .update({ id: this.currentStudent.id }, student)
          .then(response => response.json()
            .then(data => {
              const index = getIndex(this.students, data.id)
              this.students.splice(index, 1, data)
              this.id = ''
              this.firstName = ''
              this.secondName = ''
              this.middleName = ''
              this.course = ''
              this.faculty = ''
              this.team = ''
            }))
      } else {
        this.$http.post('api/user/student', student)
          .then(response => response.json()
            .then(data => {
              this.students.push(data)
              this.firstName = ''
              this.secondName = ''
              this.middleName = ''
              this.course = ''
              this.faculty = ''
              this.team = ''
            }))
      }
    }
  }
}
</script>

